body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
    overflow-x: hidden;
}

.global {
    width: 100vw;
    overflow: hidden;
    background: linear-gradient(180deg, #7A57FD 0%, #6135D7 100%);
    box-shadow: 4px 2px 14px rgba(0, 0, 0, 0.05); 
}

.container {
    max-width: 1140px;
    height: 100vh;
    width: 100%;
    margin: 0 auto;
    position: relative;
}

.footer {
    width: 100vw;
    height: 81px;
    background: #200E64;
    box-shadow: 4px 2px 14px rgba(0, 0, 0, 0.05);
}

.shapes-right {
    width: 383px;
    height: 766px;
    position: absolute;
    right: 0;
    -webkit-transform: translateX(296px);
            transform: translateX(296px);
    z-index: 9;
}

.shapes-right img, .shapes-left-svg {
    width: 100%;
}

.shapes-left {
    position: absolute;
    left: 0;
    width: 450px;
    -webkit-transform: translateX(-118px);
            transform: translateX(-118px);
    margin-top: 60px;
}

.home-image {
    position: absolute;
    height: 94%;
    width: auto;
    right: 40px;
}


/*responsive*/

@media (max-width: 980px) {
    .container {
        height: 800px;
    }

    .shapes-right {
        display: none;
    }  
    
    .shapes-left{
        width: 400px;
        display: block;
        position: relative;
        margin-left: auto;
        margin-right: auto;
        float: none;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}
.header {
  max-width: 1140px;
  width: 80%;
  display: flex;
  margin: 0 auto;
  padding-top: 20px;
}

.header__logo {
  margin-top: auto;
  margin-bottom: auto;
}

.header__bars-icon {
  display: none;
}


.modal__background {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.6);
    position: absolute;
    left: 0;
    top: 0;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal__login {
    width: 525px;
    height: 479px;
    background: #6A40E4;
    border-radius: 4px;
    position: relative;
    top: -650px;
    -webkit-animation: drop 0.5s ease forwards;
            animation: drop 0.5s ease forwards;
}

.btn__close-modal {
    background-color: transparent;
    outline: none;
    width: 51px;
    height: 51px;
    background: #200E64;
    border-radius: 50%;
    border: none;
    cursor: pointer;
    position: relative;
    -webkit-transform:  translate(50%, -50%);
            transform:  translate(50%, -50%);
    float: right;
}



p {
    padding-left: 0;
    font-family: Open Sans;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 31px;
    letter-spacing: 0em;
    font-family: 'Poppins', sans-serif;
}

.login__elements {
    position: relative;
    text-align: left;
    margin: 40px;
}

.login__elements h1 {
    font-family: 'Poppins', sans-serif;
    font-weight: normal;
    color: white;
}

.login__elements span {
    display: block;
    font-weight: bold;
    color: #FFEAA4;
}

form {
    color: white;
}

input {
    width: 445px;
    height: 56px;
    background: rgba(255, 255, 255, 0.24);
    border: 1px solid #FFFFFF;
    box-sizing: border-box;
    border-radius: 4px;
    padding: 20px;
    color:#FFFFFF;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 17px;
    letter-spacing: 0em;
    cursor:context-menu;
    font-family: 'Poppins', sans-serif;
}

.login__btn {
    color: #744FF4;
    font-family: Open Sans;
    width: 132.52px;
    height: 48px;
    background: #FFD74F;
    border-radius: 4px; 
    margin: 40px;
    margin-top: 0px;
    float: right;
    cursor: pointer;
    font-family: 'Poppins', sans-serif;
}

.eyes-icon {
    position: absolute;
    right: 15px;
    top: 305px;
    cursor: pointer;
}

/*Animação do modal*/

@-webkit-keyframes drop {
    0%{
        opacity: 0;
    }
    70% { 
        -webkit-transform: translateY(700px); 
                transform: translateY(700px) 
    }
    100%{
        -webkit-transform: translateY(650px);
                transform: translateY(650px);
        opacity: 1;
    }
}

@keyframes drop {
    0%{
        opacity: 0;
    }
    70% { 
        -webkit-transform: translateY(700px); 
                transform: translateY(700px) 
    }
    100%{
        -webkit-transform: translateY(650px);
                transform: translateY(650px);
        opacity: 1;
    }
}
.header__nav {
