.wrapper input[type="radio"]{
    width: 12px;
    height: 12px;
    display: none;
}

.wrapper {
    display: flex;
    padding: 20px 15px;
    padding-left: 0px;
    justify-content: space-evenly;
}

.wrapper .option {
    width: 162px;
    height: 56px;
    background: rgba(121, 85, 232, 0.3);
    border-radius: 4px;
    margin: 0 10px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.wrapper .option-1 {
    margin-left: 0;
}

.wrapper .option span {
    font-family: 'Poppins', Open-Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    color: white;
    text-transform: uppercase;
}

.check-icon {
    position: relative;
    left: 25px;
    color: #B68300;
    opacity: 0;
}

.wrapper .option .dot {
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background: #FFFFFF;
    opacity: 0.2;
    position: relative;
    background: #FFD74F;
}

.wrapper .option .dot::before {
    position: absolute;
    content: "";
    width: 18px;
    height: 18px;
    background: #FFD74F;
    border-radius: 50%;
    opacity: 0;
}

#option-1:checked ~ .option-1 .dot::before,
#option-2:checked ~ .option-2 .dot::before {
    opacity: 0;
}

#option-1:checked ~ .option-1,
#option-2:checked ~ .option-2 {
    background: #7955E8;
    border: 1px solid #A68EEF;
    box-sizing: border-box;
    border-radius: 4px;
}

#option-1:checked ~ .option-1 .check-icon,
#option-2:checked ~ .option-2 .check-icon {
    opacity: 1;
}

/*responsive*/

@media (max-width: 980px) {
    .wrapper{
        width:100%;
    }
}
.content {
  width: 604px;
  height: 452px;
  float: right;
  margin-top: 85px;
  margin-right: 60px;
}

.content__top h1 {
  font-family: "Poppins", sans-serif;
  font-weight: normal;
  color: white;
  margin: 0;
  font-size: 48px;
  line-height: 57px;
}

.content__top span {
  display: block;
  font-weight: bold;
  color: #ffeaa4;
}

.content__mid {
  font-family: Open Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 191.68%;
  color: #ffffff;
  margin-top: 45px;
  margin-bottom: 45px;
}

.content__mid span {
  font-family: 'Poppins', sans-serif;
  font-size: 15px;
}

.mid__input {
  width: 100%;
  height: 56px;
  background: #7955e8;
  border: 1px solid #a68eef;
  box-sizing: border-box;
  border-radius: 4px;
  cursor: pointer;
  font-family: 'Poppins', sans-serif;
  font-size: 15px
}

.mid__input:hover {
  box-sizing: border-box;
  background: #7955e8;
  cursor: pointer;
}

.content__bottom {
  display: flex;
  width: 100%;
}

.content__bottom-btn {
  position: relative;
  width: 209px;
  height: 56px;
  background: #ffd74f;
  border-radius: 4px;
  color: #744ff4;
  font-family: "Poppins", Open-Sans;
  margin-top: 20px;
  margin-left: 41px;
  cursor: pointer;
}

/*responsive*/

@media (max-width: 980px) {
  .content__mid {
    display: none;
  }

  .mid__input {
    margin-top: 70px;
  }

  .content {
    display: block;
    margin-top: 60px;
    margin-left: auto;
    margin-right: auto; 
    float: none;
    width: 80%;
  }

  .content-top {
    margin-bottom: 60px;
  }

  .content__bottom {
    display: inline-block;
    margin-top: 16px;
  }

  .content__bottom-btn {
    width: 100%;
    margin-left: 0;
    margin-top: 16px;
  }
}

